            let     map_height = windowHeight - bottomNav_height;                           //   
            const   objMap = document.getElementById('map');                                //  Map
            objMap.style.height = map_height + 'px';                                        //  Map
        }
        ,asyncLoadMap: function() {
            setTimeout(() => {
