                miniMap.centerAndZoom(miniMapPoint,10);                                     // ,15
                miniMap.addControl(new BMap.NavigationControl({
                    anchor: BMAP_ANCHOR_TOP_RIGHT,
                    type: BMAP_NAVIGATION_CONTROL_SMALL
                }));

                //  (  +  +  )

                // ObjGroup,setZoom 
                const addRangeOverlay = () => {
                    /* map.clearOverlays();                                                        // 
